import React, { useState , useEffect , useRef  } from "react";
import myStyle from './Main.module.scss';
import BankAccount from './content/BankAccount';
import Report from './content/Report';
import Directions from './content/Directions';


function Main({imageSrc , activeSection , setActiveSection , param}) {
	// const [activeSection, setActiveSection] = useState(1); // 기본 섹션을 "schedule"로 설정
	  
  const [sender, setSender] = useState("");
	
  const [recipient, setRecipient] = useState(param=== "1"? "신부":"신랑"); // Default recipient
  const [message, setMessage] = useState(""); // State to manage the message

  return (
		<div className={myStyle.main}>
			<div className={myStyle.title}>
				<div className={myStyle.buttonGroup}>
					<button className={`${myStyle.customButton} ${activeSection === 1 ? myStyle.selected : ""}`} onClick={() => setActiveSection(1)} >모시는 <span className={myStyle.point}>글</span></button>
					<button className={`${myStyle.customButton} ${activeSection === 2 ? myStyle.selected : ""}`} onClick={() => setActiveSection(2)}>오시는 <span className={myStyle.point}>길</span></button>
					<button className={`${myStyle.customButton} ${activeSection === 3 ? myStyle.selected : ""}`} onClick={() => setActiveSection(3)}>전하는 <span className={myStyle.point}>곳</span></button>
				</div>
			</div>
			<div className={myStyle.content}>
				{/*  화면 출력 */}
				{activeSection === 1 && <Report imageSrc={imageSrc}/>}
				{activeSection === 2 && <Directions/>}
				{activeSection === 3 && <BankAccount 
				sender={sender} setSender={setSender} 
				recipient={recipient}  setRecipient={setRecipient} 
				message={message}  setMessage={setMessage} 
				param={param} 
				/>}

				{/* <Report imageSrc={imageSrc}/>
				<Directions />
				<BankAccount 
				sender={sender} setSender={setSender} 
				recipient={recipient}  setRecipient={setRecipient} 
				message={message}  setMessage={setMessage} 
				/> */}
			</div>
    </div>
  );
}

export default Main;
